<template>
  <div class="section">
    <div id="main-content" class="container">
      <div class="row">
        <div class="col-md-6">
          <form class="form-inline">
            <div class="form-group">
              <label for="connect"> :</label>
              <button
                v-show="connected"
                id="connect"
                class="btn btn-default"
                type="submit"
                @click="connect"
              >
                
              </button>
              <button
                v-show="!connected"
                id="disconnect"
                class="btn btn-default"
                type="submit"
                disabled="disabled"
                @click="disconnect"
              >
                
              </button>
            </div>
          </form>
        </div>
        <div class="col-md-6">
          <form class="form-inline" @submit.prevent>
            <div class="form-group">
              <label for="msg"></label>
              <input
                type="text"
                v-model="msg"
                class="form-control"
                placeholder=" ...."
              />
            </div>
            <button
              v-show="!connected"
              id="send"
              class="btn btn-default"
              disabled
              type="submit"
              @click="sendMessage"
            >
              
            </button>
          </form>
        </div>
      </div>
      <div class="row">
        <div class="col-md-12">
          <table
            id="conversation"
            class="table table-striped"
            v-show="connected"
          >
            <thead>
              <tr>
                <th></th>
              </tr>
            </thead>
            <tbody id="communicate">
              <tr v-for="(msg, index) in message" :key="index">
                <td>{{ msg }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import SockJS from "sockjs-client";
import Stomp from "stompjs";

export default {
  name: "chat-page",
  components: {},
  data() {
    return {
      stompClient: null,
      message: [],
      msg: "",
      connected: true,
    };
  },
  methods: {
    setConnected(connected) {
      //   $("#connect").prop("disabled", connected);
      //   $("#disconnect").prop("disabled", !connected);
      //   $("#send").prop("disabled", !connected);
      this.connected = connected;
      //   if (connected) {
      //     $("#conversation").show();
      //   } else {
      //     $("#conversation").hide();
      //   }
      //   $("#msg").html("");
      this.msg = "";
    },
    connect() {
      var socket = new SockJS("/ws");
      // this.stompClient = Stomp.over(socket);
      this.stompClient = Stomp.overWS(socket);
      this.stompClient.connect({}, function (frame) {
        // this.setConnected(true);
        this.connected = true;
        this.message = "";
        console.log("Connected: " + frame);
        this.stompClient.subscribe("/topic/public", function (message) {
          this.message.push(" : " + message.body);
          //   this.showMessage(" : " + message.body); //     
        });
      });
    },
    disconnect() {
      if (this.stompClient !== null) {
        this.stompClient.disconnect();
      }
      // this.setConnected(false);
      this.connected = false;
      this.message = "";
      console.log("Disconnected");
    },
    sendMessage() {
      //   let message = $("#msg").val();
      //   this.showMessage(" : " + this.msg);
      this.message.push(" : " + this.msg);

      this.stompClient.send("/sendMessage", {}, JSON.stringify(this.msg)); //  
    },
    // showMessage(message) {
    //   $("#communicate").append("<tr><td>" + message + "</td></tr>");
    // },
  },
};
</script>

<style></style>
